<!-- 园区收入实时分析 -->
<template>
  <van-row style="height:100%">
    <!-- 顶部蓝色框 -->
    <van-row type="flex"
             align="center"
             class="dt-title">
      <van-col span="2">
        <van-icon class="dt-back"
                  @click="back"
                  name="arrow-left" />
      </van-col>
      <van-col span="20">
        <p class="dt-home-shouye">园区收入实时分析</p>
      </van-col>
    </van-row>

    <van-row class="dt-content">
      <van-row class="dt-table">
        <van-row class="dt-top-box">
          <div class="dt-top-item">
            <van-image class="top-img"
                       :src="require('@/assets/img/今日售票数.png')"></van-image>
            <div class="top-text">今日售票数</div>
            <div class="top-num">{{toDayNum}}</div>
          </div>
          <div class="dt-top-item">
            <van-image class="top-img"
                       :src="require('@/assets/img/今日销售额.png')"></van-image>
            <div class="top-text">今日销售额</div>
            <div class="top-num">{{toDayYuan}}{{toDayYuanText}}</div>
          </div>
          <div class="dt-top-item">
            <van-image class="top-img"
                       :src="require('@/assets/img/综合均价.png')"></van-image>
            <div class="top-text">综合均价</div>
            <div class="top-num">{{getAveragePrice}}</div>
          </div>
        </van-row>
        <van-row class="dt-tongji">
        </van-row>
        <div class="dt-chart-box">
          <ticketBar title="票务收入分析（近七日）"
                     :option="option1"></ticketBar>
        </div>
      </van-row>
    </van-row>
  </van-row>
</template>

<script>
//这里可以导入其他文件（比如：组件，工具js，第三方插件js，json文件，图片文件等等）
//例如：import 《组件名称》 from '《组件路径》';
import ticketBar from "@/components/echarts/ticketBar";
import {
  tourismIncome,
  ticketNumber
} from "@/api/chart";
export default {
  //import引入的组件需要注入到对象中才能使用
  components: {
    ticketBar
  },
  data () {
    //这里存放数据
    return {
      option1: {},
      toDayNum: 0,
      toDayYuan: 0,
      averagePrice: 0,
      toDayYuanText: '元',
      toDayYuanss: 0,
    };
  },
  //监听属性 类似于data概念
  computed: {
    getAveragePrice () {
      if (this.toDayYuanss == 0 || this.toDayNum == 0) {
        return 0
      }
      return (this.toDayYuanss / this.toDayNum).toFixed(2)
    }
  },
  //监控data中的数据变化
  watch: {},
  //生命周期 - 创建完成（可以访问当前this实例）
  created () {
    this.getTicketNumber()
    this.getTourismIncome()
  },
  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted () {

  },
  beforeCreate () { }, //生命周期 - 创建之前
  beforeMount () { }, //生命周期 - 挂载之前
  beforeUpdate () { }, //生命周期 - 更新之前
  updated () { }, //生命周期 - 更新之后
  beforeDestroy () { }, //生命周期 - 销毁之前
  destroyed () { }, //生命周期 - 销毁完成
  activated () { }, //如果页面有keep-alive缓存功能，这个函数会触发
  //方法集合
  methods: {

    //票务收入分析
    getTourismIncome () {
      tourismIncome('day').then(res => {
        let dataList = res.data
        let ticketList = dataList.slice(-7)
        let xData = []
        let seriesData = []
        let aNum = 0
        ticketList.forEach(item => {
          xData.push(item.createTime)
          seriesData.push(item.ticketIncome)
          aNum += Number(item.ticketIncome)
        });
        let toDayYuan = ticketList[ticketList.length - 1].ticketIncome
        this.toDayYuanss = toDayYuan
        if (toDayYuan > 10000) {
          this.toDayYuan = (toDayYuan / 10000).toFixed(2)
          this.toDayYuanText = '万元'
        } else {
          this.toDayYuan = toDayYuan
          this.toDayYuanText = '元'
        }
        console.log(aNum);
        this.option1 = {
          xAxis: {
            data: xData
          },
          series: [
            {
              data: seriesData
            }
          ]
        }
      })
    },
    getTicketNumber () {
      ticketNumber().then(res => {
        console.log("res", res);
        let data = res.data
        this.toDayNum = data[data.length - 1].number
      })
    },

    back () {
      this.$store.commit("active", 0)
      history.back(-1);
    },
  },
}
</script>
<style lang='scss' scoped>
//@import url(); 引入公共css类
@import '../../../styles/dimens';
.dt-title {
  width: 100%;
  height: $headerHeight;
  padding-top: $statusBarHeight;
  background-color: #5870f8;
}
.dt-back {
  height: px2rem(14);
  width: px2rem(14);
  margin-left: px2rem(6);
  color: #fff;
  font-size: px2rem(18);
}
.dt-home-shouye {
  font-size: px2rem(17);
  font-family: 'SourceHanSansCN-Medium';
  color: #ecedf2;
}
.dt-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: calc(100% - #{$headerHeight} - #{$statusBarHeight});
  overflow-y: auto;
  background-color: #edf1f5;
  .dt-table {
    float: left; // 为了清除父子元素外边距重叠
    width: 94%;
    .dt-top-box {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      margin-top: px2rem(10);
      background: #ffffff;
      box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.1);
      border-radius: 8px;
      .dt-top-item {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 30%;
        height: px2rem(130);
        .top-img {
          height: px2rem(30);
        }
        .top-text {
          font-family: PingFangSC-Regular;
          font-size: 11px;
          color: #666666;
          letter-spacing: -0.76px;
          line-height: px2rem(25);
        }
        .top-num {
          font-family: Helvetica-Bold;
          font-size: 18px;
          color: #333333;
          letter-spacing: 0.17px;
          text-align: center;
          line-height: px2rem(25);
        }
      }
    }
  }

  .dt-item-title {
    margin-left: px2rem(20);
    text-align: left;
    font-family: PingFangSC-Medium;
    font-size: 17px;
    font-weight: 800;
    color: #333333;
    letter-spacing: -1px;
  }
  .dt-tongji {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    margin-top: px2rem(15);
  }
  .dt-picture {
    width: px2rem(36);
    height: px2rem(36);
  }
}
.dt-text {
  width: 80%;
  padding-left: 10%;
  height: px2rem(24);
  font-family: Source Han Sans CN;
  font-weight: 500;
  color: #4c4c4c;
  font-size: px2rem(12);
  text-align: center;
  margin-top: px2rem(9);
}
.dt-num {
  text-align: center;
  vertical-align: middle;
  width: px2rem(80);
  height: px2rem(12);
  font-size: px2rem(15);
  font-family: Source Han Sans CN;
  font-weight: 500;
  color: #63c162;
  line-height: px2rem(24);
  margin-bottom: px2rem(16);
}
.dt-biao2 {
  margin-top: px2rem(10);
  margin-bottom: px2rem(70);
}
.dt-bac {
  height: px2rem(102);
  width: px2rem(87);
  background-image: linear-gradient(#ffffff, #f1f7f9);
}

.dt-chart-box {
  width: 100%;
  height: px2rem(250);
  margin-top: px2rem(10);
  background: #ffffff;
  box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.1);
  border-radius: 8px;
}
/deep/.van-image__img {
  display: block;
  width: auto;
  height: 100%;
}
</style>